html {
  font-size: 16px;
  line-height: 1.15;
}
body {
  background: #fff;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  color: #000;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
img {
  max-width: 100%;
}
.visually-hidden,
.footer__description-subscribe label {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.container {
  min-width: 320px;
  max-width: 1920px;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}
.wrapper {
  min-width: 320px;
  max-width: 1230px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}
.menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.menu__item {
  flex: 0 0 auto;
  padding: 13px 0;
  margin: 0 50px 0 0;
  position: relative;
  cursor: pointer;
}
.menu__item::after {
  content: '';
  background-color: #fce38a;
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 10%;
  height: 4px;
  opacity: 0;
  transform: translateX(-50%);
  transition: 0.3s width, 0.3s opacity;
  box-sizing: border-box;
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__item:hover::after {
  width: 100%;
  opacity: 1;
}
.menu__item:hover .menu__link {
  color: #fce38a;
}
.menu__item--active::after {
  width: 100%;
  opacity: 1;
}
.menu__item--active .menu__link {
  color: #fce38a;
}
.menu__link {
  font-weight: 400;
  font-size: 0.875rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.3s color;
}
.menu__link:focus {
  outline: none;
}
@media (max-width: 768px) {
  .menu {
    justify-content: center;
  }
}
@media (max-width: 600px) {
  .menu {
    justify-content: space-between;
  }
  .menu__item {
    margin-right: 0;
  }
}
.descriptions__subtitle {
  font-weight: 400;
  font-size: 1.5rem;
  font-family: 'Kaushan Script', cursive;
  line-height: 1.2;
  color: #333333;
  text-align: center;
  margin: 0 0 15px 0;
}
.descriptions__title {
  font-weight: 700;
  font-size: 1.875rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  margin: 0 0 40px 0;
  padding: 0 0 35px 0;
}
.descriptions__title::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: #f38181;
}
.descriptions__text {
  max-width: 980px;
  font-size: 0.9375rem;
  line-height: 1.6;
  color: #999999;
  text-align: center;
  margin: 0 auto;
}
.descriptions--work .descriptions__text {
  padding: 0 15px;
  box-sizing: border-box;
}
@media (max-width: 980px) {
  .descriptions__title {
    margin: 0 0 20px 0;
    padding: 0 0 20px 0;
  }
}
.element {
  display: block;
  position: relative;
  box-shadow: 0 0 0 0 #95e1d3;
  transform: translate(0, 0);
  transition: 0.5s box-shadow, 0.5s transform;
}
.element:hover,
.element:focus {
  box-shadow: 10px 10px 0 0 #95e1d3;
  transform: translate(-10px, -10px);
  outline: none;
}
.element:hover::before,
.element:focus::before {
  opacity: 1;
}
.element:hover .element__info,
.element:focus .element__info {
  opacity: 1;
}
.element::before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(243, 129, 129, 0.9) 100%);
  z-index: 1;
  opacity: 0;
  transition: 0.5s opacity;
}
.element__img {
  height: 250px;
}
.element__img img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.element__info {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
  opacity: 0;
  transition: 0.5s opacity;
}
.element__svg {
  width: 32px;
  height: 24px;
  fill: #fff;
}
.element__title {
  font-weight: 700;
  font-size: 1.125rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  margin: 8px 0 0 0;
}
.element__subtitle {
  font-style: italic;
  font-weight: 300;
  font-size: 0.9375rem;
  color: #fff;
  text-align: center;
  margin: 0;
}
.element__social {
  background-color: #fce38a;
  box-sizing: border-box;
  width: 54px;
  height: 54px;
  display: flex;
  border-right: 1px solid #f9bd87;
  transition: 0.4s background-color;
  outline: none;
}
.element__social:last-child {
  border-right: none;
}
.element__social:hover,
.element__social:focus {
  background-color: #f38181;
}
.element__social:hover .element__svg,
.element__social:focus .element__svg {
  fill: #fff;
}
.element--team .element__img {
  height: 470px;
}
.element--team .element__info {
  flex-direction: row;
}
.element--team .element__svg {
  margin: auto;
  fill: #f38181;
  transition: 0.4s fill;
}
.element--work:hover,
.element--work:focus {
  box-shadow: none;
  transform: translate(0, 0);
}
.element--work .element__img {
  height: 100%;
}
.element--work .element__svg {
  width: 30px;
  height: 30px;
}
.element--work .element__title {
  font-weight: 400;
  font-size: 0.875rem;
  margin: 20px 0 15px 0;
}
.element--focus {
  box-shadow: 10px 10px 0 0 #95e1d3;
  transform: translate(-10px, -10px);
  outline: none;
}
.element--focus::before {
  opacity: 1;
}
.element--focus .element__info {
  opacity: 1;
}
.header {
  position: absolute;
  z-index: 4;
  width: 100%;
  user-select: none;
}
.header__wrapper {
  padding-top: 20px;
  padding-bottom: 15px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.header__logo {
  flex: 0 0 auto;
  margin: 0 auto 0 0;
  font-weight: 700;
  font-size: 1.875rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  color: #fff;
}
.header__nav {
  margin: 0 45px 0 0;
}
.header__icons {
  display: flex;
  align-items: center;
}
.header__icons-link {
  flex: 0 0 auto;
  display: block;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
  outline: none;
}
.header__icons-link:first-child {
  margin: 0 35px 0 0;
}
.header__icons-link:first-child .header__icons-svg {
  height: 15px;
}
.header__icons-link:hover .header__icons-svg,
.header__icons-link:focus .header__icons-svg {
  fill: #fce38a;
}
.header__icons-svg {
  fill: #fff;
  width: 18px;
  height: 18px;
  transition: 0.3s fill;
}
@media (max-width: 768px) {
  .header__wrapper {
    justify-content: space-between;
  }
  .header__logo {
    flex: 0 0 auto;
    margin: 0;
    order: 1;
  }
  .header__nav {
    flex: 0 0 100%;
    margin: 2px 0 0 0;
    order: 3;
  }
  .header__icons {
    flex: 0 0 auto;
    order: 2;
  }
}
.slider {
  min-height: 100vh;
  position: relative;
  user-select: none;
  background-color: rgba(243, 129, 129, 0.9);
}
.slider__carousel {
  min-height: inherit;
}
.slider__carousel .owl-stage-outer,
.slider__carousel .owl-stage,
.slider__carousel .owl-item {
  min-height: inherit;
}
.slider__element {
  width: 100%;
  min-height: inherit;
  box-sizing: border-box;
  background: url("../img/first-page-img.jpg") center center / cover no-repeat;
  position: relative;
}
@supports (-webkit-appearance:none) {
  .slider__element {
    background: url("../img/first-page-img.webp") center center / cover no-repeat;
  }
}
.slider__element::before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(243, 129, 129, 0.9) 100%);
  z-index: 1;
}
.slider__element--bg_blue::before {
  background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(137, 129, 243, 0.9) 100%);
}
.slider__element--bg_green::before {
  background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(96, 197, 89, 0.9) 100%);
}
.slider__element--bg_pink::before {
  background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(243, 129, 242, 0.9) 100%);
}
.slider__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: inherit;
  position: relative;
  z-index: 3;
  text-align: center;
}
.slider__subtitle {
  font-weight: 400;
  font-size: 4.5rem;
  font-family: 'Kaushan Script', cursive;
  line-height: 1.2;
  color: #fff;
  margin: 0 0 35px 0;
}
.slider__title {
  font-weight: 700;
  font-size: 9.375rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 0.95;
  color: #fff;
  text-transform: uppercase;
  margin: 0 0 50px 0;
  padding: 0 0 60px 0;
  position: relative;
}
.slider__title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background-color: #fff;
}
.slider__button {
  display: block;
  padding: 9px 30px;
  border: 4px solid #fff;
  font-weight: 700;
  font-size: 0.875rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background-color: transparent;
  transition: 0.4s background-color, 0.4s border, 0.4s color;
  outline: none;
}
.slider__button:hover {
  background-color: rgba(252, 227, 138, 0.9);
  border-color: rgba(243, 129, 129, 0.9);
  color: rgba(243, 129, 129, 0.9);
}
.slider__nav {
  position: absolute;
  bottom: 0;
  left: 50%;
  z-index: 4;
  transform: translateX(-50%);
  width: 100%;
}
.slider__wrapper-nav {
  display: flex;
  justify-content: space-between;
}
.slider__link {
  display: block;
  flex: 0 0 23.33333333%;
  font-weight: 400;
  font-size: 1.125rem;
  font-family: 'Montserrat', sans-serif;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  border-top: 4px solid #fff;
  padding: 22px 0;
  box-sizing: border-box;
  opacity: 0.7;
  position: relative;
  transition: 0.3s opacity;
  outline: none;
}
.slider__link span {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 26px;
}
.slider__link::before {
  content: '';
  position: absolute;
  top: -4px;
  left: 0;
  width: 25%;
  height: 4px;
  background-color: #f38181;
  opacity: 0;
  transition: 0.3s opacity;
}
.slider__link:hover {
  opacity: 1;
}
.slider__link:hover::before {
  opacity: 1;
}
.slider__link--active {
  opacity: 1;
}
.slider__link--active::before {
  opacity: 1;
}
.slider .owl-dots {
  position: absolute;
  z-index: 5;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.slider .owl-dots .owl-dot {
  width: 18px;
  height: 18px;
  border: 2px solid #fff;
  border-radius: 50%;
  margin: 0 12px 20px 0;
  outline: none;
}
.slider .owl-dots .active {
  background-color: #fff;
}
@media (max-width: 980px) {
  .slider__element {
    background: url("../img/first-page-img-980.jpg") center center / cover no-repeat;
  }
  @supports (-webkit-appearance:none) {
    .slider__element {
      background: url("../img/first-page-img-980.webp") center center / cover no-repeat;
    }
  }
  .slider__subtitle {
    font-size: 2.25rem;
  }
  .slider__title {
    font-size: 4.6875rem;
  }
}
@media (max-width: 980px) and (orientation: landscape) {
  .slider {
    min-height: 600px;
  }
  .slider__element {
    padding: 80px 0 80px 0;
  }
}
@media (max-width: 768px) {
  .slider__element {
    background: url("../img/first-page-img-768.jpg") center center / cover no-repeat;
  }
  @supports (-webkit-appearance:none) {
    .slider__element {
      background: url("../img/first-page-img-768.webp") center center / cover no-repeat;
    }
  }
  .slider__subtitle {
    margin-bottom: 25px;
  }
  .slider__title {
    margin-bottom: 35px;
    padding-bottom: 40px;
  }
  .slider__nav {
    display: none;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .slider {
    min-height: 500px;
  }
  .slider__element {
    padding: 120px 0 50px 0;
  }
}
@media (max-width: 480px) {
  .slider__element {
    background: url("../img/first-page-img-480.jpg") center center / cover no-repeat;
  }
  @supports (-webkit-appearance:none) {
    .slider__element {
      background: url("../img/first-page-img-480.webp") center center / cover no-repeat;
    }
  }
  .slider__subtitle {
    font-size: 1.5rem;
  }
  .slider__title {
    font-size: 3.125rem;
  }
}
.about {
  background-color: #fff;
}
.about__wrapper {
  padding-top: 80px;
