.map {
  background: url("../img/map-bg.jpg") center center / cover no-repeat;
  position: relative;
}
.map:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.85);
}
@supports (-webkit-appearance:none) {
  .map {
    background: url("../img/map-bg.webp") center center / cover no-repeat;
  }
}
.map__wrapper {
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
  z-index: 2;
}
.map__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  user-select: none;
  outline: none;
}
.map__svg {
  fill: #f38181;
  width: 20px;
  height: 25px;
}
.map__title {
  font-weight: 700;
  font-size: 1.5rem;
  font-family: 'Montserrat', sans-serif;
  color: #f38181;
  text-transform: uppercase;
  text-align: center;
  margin: 10px 0 0 0;
  padding: 0 0 20px 0;
  position: relative;
  display: block;
}
.map__title::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: #f38181;
}
.map__content {
  margin: 30px 0 0 0;
  height: 450px;
  border: 3px solid #f38181;
}
.map__content iframe {
  width: 100%;
  height: 100%;
  border: none;
}
